function UserRow(props) {
  const user = props.user
  const userLink = `/users/${user.patient_details.id}`
  const updateuser = `user/update/${user.patient_details.id}`
 

  const getBadge = (status) => {
    return status === 'Active' ? 'success' :
      status === 'Inactive' ? 'secondary' :
        status === 'Pending' ? 'warning' :
          status === 'Banned' ? 'danger' :
            'primary'
  }

  return (
    <tr key={user.patient_details.id.toString()}>
      <th scope="row">{user.patient_details.id}</th>
      <td>{user.patient_details.patient_first_name}</td>
      <td>{user.patient_details.patient_last_name}</td>
      <td>{user.patient_details.emr_id}</td>
      <td>{user.patient_details.bed_no}</td>
    </tr>
  )
}

